import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { RequestOptions } from '@angular/http';
import { Router } from '@angular/router'

@Injectable({
  providedIn: 'root'
})
export class AuthService {


  headers: HttpHeaders;
  options: RequestOptions;


  private _registerUrl = "http://195.201.120.175:3000/api/register";
  private _loginUrl = "http://195.201.120.175:3000/api/auth";
  private loginViewerURL = 'http://195.201.120.175:3000/api/auth/login';
  httpOptions;
 
  constructor(private http: HttpClient,
    private _router: Router) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': 'q=0.8;application/json;q=0.9'
      })
    };
    // this.options = new RequestOptions({ headers: this.headers });
  }

  registerUser(user) {
    return this.http.post<any>(this._registerUrl, user);
  }

  loginUser(user) {
    return this.http.post<any>(this._loginUrl, user);
  }

  loginViewer(user){
    return this.http.post<any>(this.loginViewerURL, user);
  }
  
  logoutUser() {
    localStorage.removeItem('x-auth-token');
    console.log('Logging out from application');
    this._router.navigate(['/']);
  }

  getToken() {
    return localStorage.getItem('x-auth-token')
  }

  loggedIn() {
    return !!localStorage.getItem('x-auth-token')
  }
}
