import { AuthService } from './../auth.service';
import { IfrmaeService } from './../ifrmae/ifrmae.service';
import { ProfileService } from './../profile/profile.service';
import { SettingsService } from './../settings/settings.service';
import { LiveSessionService } from './../live-session/live-session.service';
import { WebinarDetail } from './../models/webinarDetails';
import { Webinar } from './../models/webinar';
import { DashboardService } from './dashboard.service';
import { WebinarService } from './../webinar.service';
import { LoginComponent } from './../login/login.component';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Input, Output, HostListener, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, Validator, Validators, FormBuilder } from '@angular/forms';
import { HttpHeaders } from '@angular/common/http';


import {Popup} from 'ng2-opd-popup';
import { Router } from '@angular/router';

declare var $: any;
@Component({
  selector: 'app-dashoboard',
  templateUrl: './dashoboard.component.html',
  styleUrls: ['./dashoboard.component.css']
})


export class DashoboardComponent implements OnInit {
  adminID: string;
  email: string;
  name: string;
  address: string;
  f_name: string;
  l_name: string;
  webinars: string[];
  UTC: string[];
  webinarIDList: string[];
  web_name: string = '';
  web_date: string = '';
  web_day: string = '';
  web_start_time: string = '';
  web_description: string = '';
  web_utc: string = '';
  web_topic: string= ''; 
  upcomingWebinar = [];
  previousWebinar = [];
  
  newdate: Date;
  dt;
  webinar: Webinar;
  // webinarDetail: WebinarDetail
  webinarDetail = {}
  webinarObject: {};
  more;
  paragraph;
  btndelete;
  rForm: FormGroup;

  fnameAlert: string = 'First Name is required';
 
  @ViewChild('popup1') popup1: Popup;



  constructor(private webinarService:WebinarService,
    private dashboardService:DashboardService,
    private _router: Router,
    private popup:Popup,
    private liveSessionService:LiveSessionService,
    private settingsService:SettingsService,
    private profileService:ProfileService,
    private ifrmaeService:IfrmaeService,
    private authService:AuthService
  ) { 
      this.popup.options = {
        header: "Webinar Detail",
        color: "#283593", // red, blue....
        widthProsentage: 40, // The with of the popou measured by browser width
        animationDuration: 0.5, // in seconds, 0 = no animation
        showButtons: true, // You can hide this in case you want to use custom buttons
        confirmBtnContent: "Start", // The text on your confirm button
        cancleBtnContent: "Delete", // the text on your cancel button
        confirmBtnClass: "btn btn-bold", // your class for styling the confirm button
        cancleBtnClass: "btn btn-bold", // you class for styling the cancel button
        animation: "fadeInUp" // 'fadeInLeft', 'fadeInRight', 'fadeInUp', 'bounceIn','bounceInDown'
      };

      if(this.token){
        _router.navigate(['/dashboard']);
      }
  }

  ngOnInit() {

    this.adminID = sessionStorage.getItem('adminID');
    this.getListOfWebinar();
  }
  
  ngAfterViewInit(){
    $.getScript('assets/js/custom.js');
  }
  
  get token() {
    return localStorage.getItem('x-auth-token');
  }

 

  registerWebinar(){
    console.log('Webinar Method called....!!');

    let startTempTime= '2018-10-15T'+this.web_start_time+':00Z';
    this.webinar = new Webinar();
    this.webinar.web_day = this.web_day;
    console.log(this.webinar.web_date);
    this.webinar.web_date = new Date(this.web_date);
    this.webinar.web_description = this.web_description;
    this.webinar.web_name = this.web_name;
    this.webinar.web_start_time = startTempTime
    this.webinar.web_topic = this.web_topic;
    this.webinar.web_utc = this.web_utc;
    console.log('this.webinar ',this.webinar);
    // let newDate = this.date+'T23:00:00.000Z'
    this.dashboardService.registerWebinar(this.webinar)
    .subscribe(
      res => {
        console.log('Response Data from Register web....', res);  
        location.reload();      
       
      },
      err => console.log(err)
    )
    this._router.navigate(['/dashboard']);
    // location.reload();
  }

  getListOfWebinar(){

    this.webinarService.getWebinar()
    .subscribe(
      res => {
        this.webinars = res.object;
        console.log('WEBINAR OBJECT ', res );
        for(var i = 0; i < res.object.length; i++){
          var date = new Date();
          var date1 = new Date(res.object[i].web_date);
          if(date1 >= date){
            this.upcomingWebinar.push(res.object[i]);
          }else {
            this.previousWebinar.push(res.object[i]);
          }
        }
        console.log('Upcoming...', this.upcomingWebinar);
        console.log('Previous...', this.previousWebinar);

      
    
        this.UTC = res.object;
      },
      err => console.log(err)
    ) 
  }

  getWebinarDetailById(id){
    this.webinarService.getWebinarById(id)
    .subscribe(
      res => {
        this.webinarDetail = res.object;
        this.popup1.show(this.popup.options);
        localStorage.setItem('_id', res.object._id);
        console.log('_id',localStorage.getItem('_id'));

        localStorage.setItem('createdAt', res.object.createdAt);
        localStorage.setItem('updatedAt', res.object.updatedAt);
        localStorage.setItem('web_date', res.object.web_date);
        localStorage.setItem('web_day', res.object.web_day);
        localStorage.setItem('web_name', res.object.web_name);
        localStorage.setItem('web_start_time', res.object.web_start_time);
        localStorage.setItem('web_topic', res.object.web_topic);
        console.log(localStorage.getItem('web_day'));
        console.log('Response from get webinar detail by id method... ', this.webinarDetail);
      },
      err => console.log(err)
    ) 
  }

  popupShow(){
    this.popup1.show(this.popup.options);
  }

  morecontent(){
    this.paragraph = $('#paragraph');
    console.log("VAlue comes here", this.paragraph[0]);
      // e.stopPropagation();
    $('#paragraph').css({
        'height': 'auto'
    });
  }
  
  buttonlock(){
      this.btndelete=$('#btn-delete');
      console.log("VAlue comes here", this.btndelete);
       $('#btn-delete').removeClass('lock-button').addClass('unlock-button');
        $('#button-icon').html('lock_open');
 
  }



  deleteWebinarById(id){
    this.webinarService.deleteWebinarById(id).subscribe(
      res => {
        console.log('Response by API...', res);
        this.popup1.hide();
        this._router.navigate(['/dashboard']);
        location.reload();
      },
      err => console.log(err)
    )
  }

  startLiveSession(){
    this.liveSessionService.startLive();
  }

  dashboard(){
    console.log('Click on Home button');
    this._router.navigate(['/dashboard']);
  }

  liveSession(){
    console.log('Im liveSession method...!!!');
    this.ifrmaeService.onSubmit();
  }

}
