import { Password } from './../models/password';
import { SettingsService } from './settings.service';
import { Component, OnInit } from '@angular/core';
import { AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { Settings } from '../models/settings';
import { FormGroup, Validator, Validators, FormBuilder } from '@angular/forms';


declare var $: any;
@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})


export class SettingsComponent implements OnInit {
  settingsData = {}
  password: Password;
  adminID;
  _id: string;
  oldPassword: string = '';
  newPassword: string = '';
  newPassword1: string = '';
  settings: Settings;
  rForm: FormGroup;
  passwordStatus: string = '';

  oldPasswordAlert: string = 'Password is required';
  newPasswordAlert: string = 'Password is required';
  newPasswordAlert1: string = 'Password is required';

  
  constructor(
    private settingsService:SettingsService,
    private _router: Router,
    private fb: FormBuilder) {
      this.settings = new Settings();
      this.settings._id = sessionStorage.getItem('adminID');
      console.log('adminID in Settings', this.settings._id);

      this.rForm = fb.group({
        'oldPassword': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(15)])],
        'newPassword': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(15)])],
        'newPassword1': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(15)])],
        'validate': ''
      });
    }

  ngOnInit() {
   
  }
  
  ngAfterViewInit(){
     $.getScript('assets/js/custom.js');
  }
  changeSettings(){
    this.settings.oldPassword = this.oldPassword;
    this.settings.newPassword = this.newPassword;
    this.settings.newPassword1 = this.newPassword1;
    
    if(this.newPassword === this.newPassword1){
      this.settingsService.changeSettings(this.settings)
      .subscribe(
        res => {
          console.log('Response By Settings api', res.status);
          this.passwordStatus = res.status;
          // location.reload();
          // this._router.navigate(['/settings']);
        },
        err => {
          console.log('Error in changeSettings method ',err);
        }
      )
    }else{
      console.log('Passwords donot match');
    }
    
  }

}
