import { Router } from '@angular/router';
import { AuthService } from './../auth.service';
import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { Login } from '../models/login';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DOCUMENT } from '@angular/common';

declare var $: any;
@Component({
  selector: 'app-login-webinar',
  templateUrl: './login-webinar.component.html',
  styleUrls: ['./login-webinar.component.css']
})
export class LoginWebinarComponent implements OnInit {
  login: Login;
  email;
  name;
  rForm: FormGroup;
  emailAlert: string = 'Email is required';
  nameAlert: string = 'Name is required';

  constructor(
    @Inject(DOCUMENT) private document: any,
    private _auth: AuthService,
    private _router: Router,
    private fb: FormBuilder) {
    this.rForm = fb.group({
      'email': [null, Validators.required],
      'name': [null, Validators.required]
    });
  }

  ngOnInit() {
  }

  loginUser() {
    this.login = new Login();
    this.login.email = this.email;
    this.login.name = this.name;
    console.log('Login object ', this.login);

    this._auth.loginViewer(this.login)
      .subscribe(
        res => {
          console.log('Response Data', res);
          if (res.status === 'success') {
            console.log('Status is true');
            var viewerID = res.object._id;
            var webinarID  = localStorage.getItem('_id')
            console.log('ID IN VIEWER LOGIN', webinarID);
            this.document.location.href = 
            'https://195.201.120.175:8443/?id='+ webinarID + '&vid=' + viewerID + '&v=10';
          }
        },
        err => console.log(err)
      )
  }

}
