import { SettingsComponent } from './settings/settings.component';
import { ProfileComponent } from './profile/profile.component';
import { LiveSessionComponent } from './live-session/live-session.component';
import { DashoboardComponent } from './dashoboard/dashoboard.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component'
import { AuthGuard } from './auth.quard';
import { IfrmaeComponent } from './ifrmae/ifrmae.component';
import { LoginWebinarComponent } from './login-webinar/login-webinar.component';

const routes: Routes = [ 
  {
    path: '',
    component: LoginComponent
  },
  {
    path: 'dashboard',
    component: DashoboardComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'livesession',
    component: LiveSessionComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'profile',
    component: ProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'settings',
    component: SettingsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'session',
    component: IfrmaeComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'loginwebinar',
    component: LoginWebinarComponent
  }
  // {
  //   path: '**',
  //   redirectTo: '',
  // }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }