import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http'
import { Router } from '@angular/router'
import { throwError } from '../../node_modules/rxjs';

@Injectable({
  providedIn: 'root'
})
export class WebinarService {
  httpOptions;

  private getWebinarUrl = "http://195.201.120.175:3000/api/webinar/listOfWebinar";
  private getWebinarByID = 'http://195.201.120.175:3000/api/webinar/getById/';
  private deleteWebUrl = 'http://195.201.120.175:3000/api/webinar/getById/';
  constructor(private http: HttpClient,
    private _router: Router) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'x-auth-token'
      })
    };

    httpOptions.headers = httpOptions.headers.set('Authorization', 'x-auth-token');
    httpOptions.headers.append('x-auth-token', localStorage.getItem('x-auth-token'));
    console.log('x-auth-token : ', localStorage.getItem('x-auth-token'));
  }


  getWebinar() {
    return this.http.get<any>(this.getWebinarUrl)
    
  }

  getWebinarById(id) {
    return this.http.get<any>(this.getWebinarByID + id);
  }

  deleteWebinarById(id) {
    return this.http.delete<any>(this.deleteWebUrl + id);
  }



  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  };


}
